import React from "react";
import { Container, Row, Col, Image, Form } from "react-bootstrap";
import { Link } from "react-router-dom";
import style from "../assets/style.css";
import dashboardicon from "../assets/images/dashboardicon.png";
import premiumcontent from "../assets/images/premiumcontent.png";
import cryptoacademy from "../assets/images/cryptoacademy.png";
import portfolio from "../assets/images/portfolio.png";
import discord from "../assets/images/discord.png";
import help from "../assets/images/help.png";
import profileimg from "../assets/images/profileimg.png";
import chevron from "../assets/images/chevron.png";

import Main from "./Main";
function Sidebar() {
  return (
    <Container fluid className="px-0">
      <Row className="mx-0">
        <Col md="12" className="px-0">
          {/* <!-- Main --> */}
          <section id="main">
            <Container fluid>
              {/* <!-- SideBar --> */}
              <aside>
                <div className="sidebar-links">
                  <Link to="/" className="btn">
                    <img src={dashboardicon} alt="" />{" "}
                    <span className="drop-txt">Dashboard</span>
                  </Link>

                  <Link to="/premium" className="btn">
                    <img src={premiumcontent} alt="" />{" "}
                    <span className="drop-txt">Premium Content</span>
                  </Link>

                  <Link to="/cryptoacademy" className="btn">
                    <img src={cryptoacademy} alt="" />{" "}
                    <span className="drop-txt">Crypto Academy</span>
                  </Link>

                  <Link to="/portfolio" className="btn">
                    <img src={portfolio} alt="" />{" "}
                    <span className="drop-txt">Portfolio</span>
                  </Link>

                  <Link to="/discord" className="btn">
                    <img src={discord} alt="" />{" "}
                    <span className="drop-txt">Discord</span>
                  </Link>

                  <Link to="/help" className="btn">
                    <img src={help} alt="" />{" "}
                    <span className="drop-txt">Help</span>
                  </Link>
                </div>
                <div className="sidebar-footer">
                  {/* <!-- Link 1 --> */}
                  <a className="btn p-0" href="#">
                    <img src={profileimg} alt="" />
                    <span className="drop-txt">
                      <p>Leo Carder</p>
                      <p>Pro Member</p>
                    </span>
                    <span className="arrow">
                      {" "}
                      <img src={chevron} alt="" />
                    </span>
                  </a>
                </div>
              </aside>
              {/* <!-- Main Content --> */}
              <Main />
            </Container>
          </section>
        </Col>
      </Row>
    </Container>
  );
}

export default Sidebar;
