import { Container, Row, Col, Image, Form } from "react-bootstrap";
import hand from "../assets/images/hand.png";
import rocket from "../assets/images/rocket.png";
import article from "../assets/images/article.png";
import video from "../assets/images/video.png";
import download from "../assets/images/download.png";
import cryptosports from "../assets/images/cryptosports.png";
import cryptostudy from "../assets/images/cryptostudy.png";
import bitcoin from "../assets/bitcoin.mp4";
import bookmark from "../assets/images/bookmark.svg";
import cryptonews from "../assets/images/cryptonews.jpg";
import nftnews from "../assets/images/nftnews.jpg";
import playbtn from "../assets/images/playbtn.svg";
import chevron from "../assets/images/chevron.png";

const Discord = () => (
  <main>
    <div className="dashboard-row">
      <Row>
        <Col md="5" className="f1st cus mt-3">
          {/* <div className="col-md-5 f1st cus mt-3"> */}
          <div className="dashboard-col dashboard-col2 discord-col">
            <div className="dashboard-col-header py-4">
              <h2>Discord Community</h2>
            </div>
            <div className="discord-content text-center">
              <h3>
                Join our Discord channel to receive live discussion updates by
                following the simple steps.
              </h3>
              <div className="discord-form">
                <Form>
                  <ul id="progressbar">
                    <li className="active"></li>
                    <li className=""></li>
                  </ul>

                  <h4>Step 1 : Login / Signup to your Discord Account</h4>
                  <h5>Join our Discord channel using the Join button below.</h5>

                  <div className="inputfield form-group position-relative">
                    <input
                      type="text"
                      className="form-control"
                      placeholder="https://discord.gg/s2BEgAg"
                    />
                    <input
                      type="submit"
                      value="Join"
                      className="position-absolute btn"
                    />
                  </div>

                  <div className="done-container">
                    <p>Once done, use the confirm button below to continue.</p>
                    <a href="/" className="btn">
                      Confirm
                    </a>
                  </div>
                </Form>
              </div>
            </div>
          </div>
          {/* </div> */}
        </Col>
        <Col md="7" className="news cus mt-3">
          <div className="dashboard-col dashboard-col2 latest-news">
            <div className="dashboard-col-header">
              <div
                className="
                d-flex
                justify-content-between
                align-items-center
              "
              >
                <h2>Latest News</h2>
                <div className="journal-sort-container">
                  <form>
                    <div className="d-flex">
                      <div className="form-group mb-0">
                        <select className="form-control" id="sort">
                          <option>Popular</option>
                          <option>Latest</option>
                          <option>Recently Viewed</option>
                        </select>
                      </div>
                      <div className="form-group mb-0">
                        <select className="form-control" id="categ">
                          <option>All News</option>
                          <option>Old</option>
                          <option>Course</option>
                          <option>Content Calender</option>
                        </select>
                      </div>
                      <div className="arrow">
                        <img src={chevron} alt="" />
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>

            <div className="dashboard-news">
              <div
                className="
                news-col news-col1
                d-flex
                justify-content-between
              "
              >
                <div className="news-img">
                  <img src={cryptonews} alt="" />
                </div>
                <div className="news-content">
                  <label>Cryptocurrency news</label>
                  <h3>
                    The Anhui province in China is the latest to ban crypto
                    mining
                  </h3>
                  <p>
                    Binance has been in the news a lot lately. The exchange was
                    forced to stop business activities in Ontario and received a
                    Binance has been in the news a lot lately. The exchange was
                    forced to stop business activities in Ontario and received a
                  </p>
                  <div
                    className="
                    news-time-container
                    d-flex
                    justify-content-between
                    align-items-center
                  "
                  >
                    <span>2 hours ago</span>
                    <button type="button" className="btn">
                      <img src={bookmark} alt="" />
                    </button>
                  </div>
                </div>
              </div>
              <div
                className="
                news-col news-col2
                d-flex
                justify-content-between
              "
              >
                <div className="news-img">
                  <img src={nftnews} alt="" />
                </div>
                <div className="news-content">
                  <label>NFT</label>
                  <h3>Space Jam Movie Launches With 91,000 NFTs</h3>
                  <p>
                    Introduction Yield farming, also known as liquidity mining,
                    has grown massively in popularity since the DeFi Summer of
                    2020 Introduction Yield farming, also known as liquidity
                    mining, has grown massively in popularity since the DeFi
                    Summer of 2020
                  </p>
                  <div
                    className="
                    news-time-container
                    d-flex
                    justify-content-between
                    align-items-center
                  "
                  >
                    <span>2 hours ago</span>
                    <button type="button" className="btn">
                      <img src={bookmark} alt="" />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  </main>
);
export default Discord;
