import { Container, Row, Col, Image } from "react-bootstrap";
import Form from "react-bootstrap/Form";
// import Select from "react-select";
import hand from "../assets/images/hand.png";
import rocket from "../assets/images/rocket.png";
import article from "../assets/images/article.png";
import video from "../assets/images/video.png";
import download from "../assets/images/download.png";
import cryptosports from "../assets/images/cryptosports.png";
import cryptostudy from "../assets/images/cryptostudy.png";
import bitcoin from "../assets/bitcoin.mp4";
import bookmark from "../assets/images/bookmark.svg";
import cryptonews from "../assets/images/cryptonews.jpg";
import nftnews from "../assets/images/nftnews.jpg";
import playbtn from "../assets/images/playbtn.svg";
import chevron from "../assets/images/chevron.png";
import Discord from "./Discord";

const Main = () => (
  <main>
    <div className="main-content">
      <div className="page-title">
        <h1>
          <span>
            <img src={hand} alt="Shaking Hand" />
          </span>{" "}
          Welcome John
        </h1>
        <p>We have curated some content for you to get started!</p>
      </div>
      <div className="dashboard-row">
        <Row>
          <Col md="5" className="f1st cus">
            {/* <div className="col-md-5 f1st cus"> */}
            <div className="dashboard-col dashboard-col1">
              <label>
                <img src={rocket} alt="Rocket" /> Recommended
              </label>
              <h2>Exclusive Crypto 101 course to get you started!</h2>
              <p>
                Canadaâ€™s largest digital-asset investment fund manager 3iQ Corp
                wants to raise $200 million by listing its Bitcoin.
              </p>
              <div className="tool-box d-flex justify-content-between align-content-center">
                <div className="tool-col tool-col1">
                  <p>
                    <img src={article} alt="" /> 20+ Articles
                  </p>
                </div>
                <div className="tool-col tool-col2">
                  <p>
                    <img src={video} alt="" /> 15 hrs of video guides
                  </p>
                </div>
                <div className="tool-col tool-col3">
                  <p>
                    <img src={download} alt="" /> 15 bonus resources
                  </p>
                </div>
              </div>
              <a href="/" className="btn">
                Get started
              </a>
            </div>
            {/* </div> */}
          </Col>
          <Col md="5" className="s2nd cus">
            {/* <div className="col-md-5 s2nd cus"> */}

            <div className="dashboard-col dashboard-col2">
              <div className="dashboard-col-header">
                <div className="d-flex justify-content-between align-items-center">
                  <h2>Journals</h2>
                  <div className="journal-sort-container">
                    <form>
                      <div className="d-flex">
                        <div className="form-group mb-0">
                          <select className="form-control" id="categ">
                            <option>Journals</option>
                            <option>What's New</option>
                            <option>Course</option>
                            <option>Content Calender</option>
                          </select>
                        </div>
                        <div className="form-group mb-0">
                          <select className="form-control" id="categ">
                            <option>Journals</option>
                            <option>What's New</option>
                            <option>Course</option>
                            <option>Content Calender</option>
                          </select>
                        </div>
                        <div className="arrow">
                          <img src={chevron} alt="" />
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
              <div className="dashboard-inner d-flex justify-content-between">
                <div className="dashboard-inner-col dashboard-inner-col1">
                  <div className="img">
                    <img src={cryptosports} alt="" />
                  </div>
                  <p>Opinion</p>
                  <h3>Teamwork makes the Dream work: Crypto x Sports</h3>
                </div>
                <div className="dashboard-inner-col dashboard-inner-col2">
                  <div className="img">
                    <img src={cryptostudy} alt="" />
                  </div>
                  <p>Explained, Opinion</p>
                  <h3>Regulations, Ransomware and Cryptocurrencies</h3>
                </div>
              </div>
            </div>
            {/* </div> */}
          </Col>
          <Col md="2" className="t3rd cus">
            {/* <div className="col-md-2 t3rd cus"> */}
            <div className="dashboard-col dashboard-col3">
              <div className="dashboard-col-video">
                <video controls>
                  <source src={bitcoin} type="video/mp4" />
                  Your browser does not support HTML video.
                </video>
              </div>
              <h3>Empowering Women in Crypto</h3>
              <p>Cryptonary</p>
              {/* <!-- Video Tools --> */}
              <div className="video-tools">
                {/* <!-- video Progress --> */}
                <div className="progress">
                  <div className="progress-bar"></div>
                </div>
                {/* <!-- video time --> */}
                <span className="video-time">00:00</span>
                {/* <!-- video Options --> */}
                <div className="video-option dropdown">
                  <button
                    type="button"
                    className="btn dropdown-toggle"
                    data-toggle="dropdown"
                  >
                    . . .
                  </button>
                  <div className="dropdown-menu">
                    <a className="dropdown-item" href="#">
                      Link 1
                    </a>
                    <a className="dropdown-item" href="#">
                      Link 2
                    </a>
                    <a className="dropdown-item" href="#">
                      Link 3
                    </a>
                  </div>
                </div>
                {/* <!-- Video Play Btn --> */}
                <button className="play-btn btn pb-0 pr-0">
                  <img src={playbtn} alt="" />
                </button>
              </div>
            </div>
            {/* </div> */}
          </Col>
          <Discord />

          {/* <!-- More News Container --> */}
          <div className="more-news-container text-right">
            <p>
              For more news
              <a href="https://www.cryptonary.com/">Cryptonary.com</a>
            </p>
          </div>
        </Row>
      </div>
    </div>
  </main>
);
export default Main;
